 * @Description: In User Settings Edit
 * @FilePath: \工作项目协同开发\operation_system\src\components\orderManagement\DailyOperatingData\phone\GuidePriceEdit.vue
-->
<template>
  <div id="guide-edit">
    <!-- 头部导航抽屉 -->
    <div class="menu">
      <i class="el-icon-right" @click="showMenu"></i>
      <div class="sku-list" v-if="isShowSku">
        <span>请选择sku：</span>
        <el-select v-model="checkedSku" placeholder="请选择" @change="skuChange" size="mini">
          <el-option v-for="(item, index) in sku_list" :key="index" :label="item" :value="item"></el-option>
        </el-select>
      </div>
    </div>
    <!-- spu展示 -->
    <div class="spu-list" v-if="spulist.length > 0">
      <span>spu:</span>
      <span
        :class="['skuSpan', { active: curIndex1 === index }]"
        @click="selectSpuFun(index, item)"
        v-for="(item, index) in spulist"
        :key="index"
      >{{ item }}</span>
      <!-- <el-button
        type="primary"
        v-for="(item, index) in spulist"
        :key="index"
        size="mini"
        @click="selectSpuFun(index, item)"
        >{{ item }}</el-button
      >-->
    </div>

    <!-- 表格展示 -->
    <div class="table">
      <!-- 第一个表格 -->
      <div class="table-one">
        <el-table
          v-loading="loading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          max-height="300"
          border
          :data="tableData"
          stripe
          size="mini"
          style="width: 100%"
        >
          <el-table-column align="center" prop="dates" label="日期" :width="tableOne.width"></el-table-column>
          <el-table-column align="center" prop="ranking" label="大类排名" :width="tableOne.width1"></el-table-column>
          <el-table-column align="center" prop="small_rank" label="小类排名" :width="tableOne.width1"></el-table-column>
          <el-table-column align="center" prop="inventory" label="库存" :width="tableOne.width2"></el-table-column>
          <el-table-column
            align="center"
            prop="comment_amount"
            label="评分数量"
            :width="tableOne.width1"
          ></el-table-column>
          <el-table-column align="center" prop="star_level" label="评分" :width="tableOne.width2"></el-table-column>
        </el-table>
        <div class="table-button">
          <el-button type="primary" size="mini" @click="uploadRank">上传排名</el-button>
          <el-button type="primary" size="mini" @click="goUrl">前台网址</el-button>
        </div>
      </div>
      <!-- 第二个表格 -->
      <div class="table-two">
        <el-table
          stripe
          v-loading="loading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          max-height="400"
          :cell-style="cellStyle"
          :data="tableData1"
          border
          style="width: 100%"
          size="mini"
        >
          <el-table-column align="center" prop="times" label="日期" fixed="left"></el-table-column>
          <el-table-column
            align="center"
            prop="automatic_guidance"
            label="实际指导价(自动)"
            :width="tableTwo.width"
          ></el-table-column>
          <el-table-column
            align="center"
            prop="sys_automatic_guidance"
            label="系统指导价(自动)"
            :width="tableTwo.width"
          ></el-table-column>
          <el-table-column
            align="center"
            prop="auto_ad_cost"
            label="实际花费(自动)"
            :width="tableTwo.width"
          ></el-table-column>
          <el-table-column
            align="center"
            prop="manual_guidance"
            label="实际指导价(手动)"
            :width="tableTwo.width"
          ></el-table-column>
          <el-table-column
            align="center"
            prop="sys_manual_guidance"
            label="系统指导价(手动)"
            :width="tableTwo.width"
          ></el-table-column>
          <el-table-column
            align="center"
            prop="no_auto_ad_cost"
            label="实际花费(手动)"
            :width="tableTwo.width"
          ></el-table-column>
          <el-table-column align="center" prop="cost_rate" label="广告比" :width="tableTwo.width"></el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 备注区域 -->
    <div class="remark">
      <div class="title">
        <span>备注相关{{ yesterday_remark }}</span>
      </div>
      <div class="editBox">
        <el-form :model="formData" :rules="rules" ref="guidepriceform" :inline="true">
          <el-form-item style="display:block">
            <el-date-picker
              v-model="date"
              :picker-options="disableOpt"
              type="date"
              placeholder="选择日期"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <div class="remark-inline">
            <el-form-Item prop="automatic_guidance" label="自动组">
              <el-input v-model="formData.automatic_guidance"></el-input>
            </el-form-Item>
            <el-form-Item prop="manual_guidance" label="手动组">
              <el-input v-model="formData.manual_guidance"></el-input>
            </el-form-Item>
          </div>
          <div class="remark-width">
            <el-form-item prop="remarks" label="备注" style="width:100%">
              <el-input class="remarkDiv" v-model="formData.remarks" type="textarea"></el-input>
            </el-form-item>
          </div>
          <el-form-item style="display:block">
            <el-button
              class="lastBtn"
              type="primary"
              @click="editSubmit('guidepriceform')"
              size="mini"
            >确定</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>

    <!-- 导航抽屉 -->
    <el-drawer
      title="同步广告指导"
      :visible.sync="drawerVisible"
      direction="ltr"
      size="60%"
      :show-close="false"
      custom-class="drawer"
    >
      <!-- 内容区域 -->
      <el-menu
        unique-opened
        @open="handleOpen"
        @select="handleselect"
        class="el-menu-vertical-demo leftMenuBar"
      >
        <el-submenu v-for="(channel, i) in menuPlatFormList" :key="i" :index="i + ''">
          <template slot="title">
            <span style="display:inline-block;width:100%">{{ channel }}</span>
          </template>
          <el-submenu v-for="(country, j) in showCountryList" :key="j" :index="i + '-' + j">
            <template slot="title">
              <span style="display:inline-block;width:100%">{{ country }}</span>
            </template>
            <el-menu-item
              v-for="(station, idx) in lastSiteList[j]"
              :key="idx"
              :index="i + '-' + j + '-' + idx"
            >
              <span style="display:inline-block;width:100%">{{ station }}</span>
            </el-menu-item>
          </el-submenu>
        </el-submenu>
      </el-menu>
    </el-drawer>

    <!-- 上传排名对话框 -->
    <el-dialog title="上传排名" :visible.sync="dialogVisible" width="100%" center>
      <div class="form-con">
        <el-form ref="rankingForm" :rules="rulesUL" :model="rankingForm">
          <el-form-item prop="date" label="日期">
            <el-date-picker
              v-model="rankingForm.date"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期"
            ></el-date-picker>
            <!-- <el-input v-model="rankingForm.date"></el-input> -->
          </el-form-item>
          <el-form-item prop="ranking" label="大类排名">
            <el-input v-model="rankingForm.ranking"></el-input>
          </el-form-item>
          <el-form-item prop="small_rank" label="小类排名">
            <el-input v-model="rankingForm.small_rank"></el-input>
          </el-form-item>
          <el-form-item prop="comment_amount" label="评分数量">
            <el-input v-model="rankingForm.comment_amount"></el-input>
          </el-form-item>
          <el-form-item prop="star_level" label="评分">
            <el-input v-model="rankingForm.star_level"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitRanking('rankingForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      FirstInitSpu: false,
      // 菜单抽屉
      drawerVisible: false,
      // 选中的sku
      checkedSku: "",
      // 是否显示sku下拉框
      isShowSku: false,
      // 前台网址
      receptionUrl: "",
      // 表格1 宽度设置
      tableOne: {
        // 时间
        width: "",
        // 大类排名，小类排名，评分数量
        width1: "",
        // 库存和评分
        width2: ""
      },
      // 表格2 宽度设置
      tableTwo: {
        // 除了时间以外的
        width: ""
      },
      // -------------------------原有的-------------------
      uploadFlag: false,
      dialogVisible: false,
      dialogVisible1: false,
      GuiPriSupForm: {
        date: "",
        auto_ad_cost: "",
        automatic_guidance: "",
        manual_guidance: "",
        no_auto_ad_cost: "",
        cost_rate: ""
      },
      openCountryFlag: false,
      loading: false,
      date: "",
      table2_lastObj: {},
      submitYet: false,
      disableOpt: {
        disabledDate: time => {
          return time.getTime() > new Date();
        }
      },
      // submitSpu: [], // 已经提交了的sku
      yesterday_remark: "", //备注文本
      rules: {
        automatic_guidance: [
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ],
        manual_guidance: [
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ]
      },
      GuidePriceSupRules: {
        date: [{ required: true, message: "请选择时间", trigger: "blur" }],
        sys_automatic_guidance: [
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ],
        auto_ad_cost: [
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ],
        sys_manual_guidance: [
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ],
        no_auto_ad_cost: [
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ]
      },
      rulesUL: {
        date: [{ required: true, message: "请选择时间", trigger: "blur" }],
        ranking: [
          { required: true, message: "请输入大类排名", trigger: "blur" },
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ],
        small_rank: [
          { required: true, message: "请输入小类排名", trigger: "blur" },
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ]
      },
      // allChannelData: [],
      menuPlatFormList: [],
      showCountryList: [],
      lastSiteList: [],
      sku_list: [],
      formData: {
        automatic_guidance: "",
        manual_guidance: "",
        remarks: ""
      },
      tableData: [],
      tableData1: [],
      channel: "",
      country: "",
      station: "",
      firstData: {},
      secondData: {},
      timelist: [],
      firstTableData: [],
      secondTableData: [],
      spu: "",
      curIndex: "",
      lastTable2: "",
      countryListIndex: "",
      firstIdx: "",
      secondIdx: "",
      thirdIdx: "",
      spuData: [],
      auto: "",
      spulist: [],
      curIndex1: "",
      rankingForm: {
        date: "",
        ranking: "",
        comment_amount: "",
        star_level: "",
        small_rank: ""
      }
      // -------------------------原有的-------------------
    };
  },
  computed: {
    curDate() {
      let dateTime = new Date();
      return (
        dateTime.getFullYear() +
        "-" +
        (dateTime.getMonth() + 1) +
        "-" +
        dateTime.getDate()
      );
    },
    // 获取屏幕宽度
    clientWidth() {
      return this.$store.state.clientWidth;
    }
  },
  watch: {
    // 监听屏幕宽度
    clientWidth(val) {
      console.log("监听", val);
      if ((val < 768 || val == 768) && (val > 400 || val === 400)) {
        console.log("大");
        this.tableOne.width1 = "";
        this.tableOne.width2 = "";
        this.tableTwo.width = "";
      } else if (val < 400 || val == 400) {
        console.log("小");
        this.tableOne.width1 = "70";
        this.tableOne.width2 = "40";
        this.tableTwo.width = "60";
      } else {
        this.tableOne.width1 = "";
        this.tableOne.width2 = "";
        this.tableTwo.width = "";
      }
    }
  },
  methods: {
    /**
     * @description: 如果是pc端则跳到pc页面
     * @param {*}
     * @return {*}
     */
    selectSpuFun(index, item) {
      //修改 不是从国家站点请求
      this.FirstInitSpu = false;
      this.selectSpu(index, item);
    },
    // 修改 选择spu后重新请求数据
    reFlashSpu(item) {
      let url = this._url2 + "server/order_management/advertising_guide/";
      this.$axios
        .get(url, {
          params: {
            country: this.country,
            site: this.station,
            auto: this.auto,
            spu: this.spu
          }
        })
        .then(res => {
          // re_spu_data
          console.log("新的请求数据", res);
          // 处理数据
          this.firstData = res.data.re_spu_data || {};
          console.log(this.firstData);
          // 处理第一个 表格

          let curItem = {};
          for (let key in this.firstData) {
            if (key === item) {
              curItem = this.firstData[key];
              break;
            }
          }
          console.log("选中的表格数据", curItem);
          // 判断选中的键是否为正确的值
          let keys = Object.keys(curItem);
          if (keys.length === 0) {
            alert("数据为{}");
            this.tableData = [];
            this.receptionUrl = "";
            return;
          }
          for (let key in curItem) {
            if (!/^\d{4}-\d{2}-\d{2}$/g.test(key)) {
              alert(curItem[key]);
              this.tableData = [];
              this.receptionUrl = "";
              console.log("跳出循环，跳出方法");
              return;
            }
            let obj = {};
            obj["dates"] = key;
            console.log(curItem[key][0]);
            for (let i in curItem[key][0]) {
              let value = curItem[key][0][i];
              // ranking small_rank star_level comment_amount
              if (
                i === "ranking" ||
                i === "small_rank" ||
                i === "star_level" ||
                i === "comment_amount"
              ) {
                if (typeof value === "string") {
                  console.log("同步值", typeof value, value);
                  value = value.replace(/^(\D*)(\d+.*)/g, "$2");
                  console.log("同步值", value);
                }
              }
              obj[i] = value;
            }
            if (this.tableData.length === 0) {
              this.receptionUrl = obj.URL;
            }
            this.tableData.push(obj);
          }
        });
    },
    isPhone() {
      let flag = navigator.userAgent.match(
        /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i
      );
      if (!flag) {
        this.$router.push("/GuidePriceedit");
      }
    },
    /**
     * @description: 展示菜单抽屉
     * @param {*}
     * @return {*}
     */
    showMenu() {
      this.drawerVisible = true;
    },
    /**
     * @description: sku选择
     * @param {*} val
     * @return {*}
     */
    skuChange(val) {
      let i = "";
      this.sku_list.forEach((item, index) => {
        if (item === val) {
          i = index;
        }
      });
      console.log(i);
      this.selectSku(i, val);
    },
    /**
     * @description: 跳转前台网址
     * @param {*}
     * @return {*}
     */
    goUrl() {
      if (this.receptionUrl !== "") {
        // this.$router.push(this.receptionUrl);
        window.location.href = this.receptionUrl;
      }
    },
    /**
     * @description: 根据屏幕尺寸动态设置表格项宽度
     * @param {*}
     * @return {*}
     */
    setWidth() {
      console.log(document.body.clientWidth);
      let val = document.body.clientWidth;
      if ((val < 768 || val == 768) && (val > 400 || val === 400)) {
        console.log("大");
        this.tableOne.width1 = "";
        this.tableOne.width2 = "";
        this.tableTwo.width = "";
      } else if (val < 400 || val == 400) {
        console.log("小");
        // this.tableOne.width = "30%";
        this.tableOne.width1 = "70";
        this.tableOne.width2 = "40";
        this.tableTwo.width = "60";
      } else {
        this.tableOne.width1 = "";
        this.tableOne.width2 = "";
        this.tableTwo.width = "";
      }
    },
    // -------------------------------原有的--------------------------
    submitGuidePrice(formName) {
      this.$refs[formName].validate(valid => {
        if (!valid) return;
        // 发送请求。
        let url = this._url2 + "server/order_management/advertising_guide/";
        // let putObj = {};
        let putObj = new FormData();
        putObj.append("site", this.station);
        putObj.append("country", this.country);
        putObj.append("spu", this.spu);
        putObj.append("auto", this.auto);
        for (let key in this.GuiPriSupForm) {
          let item = this.GuiPriSupForm[key];
          if (key === "date") {
            putObj.append("dates", item);
          } else {
            putObj.append(key, item);
          }
        }
        this.$axios.put(url, putObj).then(res => {
          if (res.data.code === 200) {
            this.$message.success("请求成功！");
            this.dialogVisible1 = false;
            // 刷新到sku部分
            this.tableData = [];
            this.tableData1 = [];
            console.log(this.curIndex);
            this.selectSku(this.curIndex);
          } else {
            this.$message.warning("请求未成功！");
          }
        });
      });
    },
    SupplementPrice() {
      if (!this.uploadFlag) return;
      this.dialogVisible1 = true;
    },
    uploadRank() {
      if (!this.uploadFlag) return;
      this.rankingForm.date = this.curDate;
      this.dialogVisible = true;
    },
    submitRanking(formName) {
      this.$refs[formName].validate(valid => {
        if (!valid) return;
        let url = this._url2 + "server/order_management/upload_rank/";
        let fd = new FormData();
        for (let key in this.rankingForm) {
          fd.append(key, this.rankingForm[key]);
        }
        fd.append("country", this.country);
        fd.append("site", this.station);
        fd.append("spu", this.spu);
        this.$axios.post(url, fd).then(res => {
          if (res.data.code === 200) {
            this.$message.success(res.data.msg);
            this.dialogVisible = false;
          } else {
            this.$message.warning("请求失败");
          }
        });
      });
    },
    selectSpu(index, item) {
      this.uploadFlag = true;
      console.log(index, item);
      this.curIndex1 = index;
      this.tableData = [];

      // spu赋值
      this.spu = item;
      console.log("当前选中auto索引", this.curIndex);
      console.log("选中的auto,spu", this.auto, this.spu, item);
      // 点击其他spu后重新请求当前的spu数据
      if (!this.FirstInitSpu) {
        this.reFlashSpu(item);
      } else {
        // 第一次点击站点国家时直接发请求。
        let curItem = {};
        for (let key in this.firstData) {
          if (key === item) {
            curItem = this.firstData[key];
            break;
          }
        }
        console.log("选中的表格数据", curItem);
        // 判断选中的键是否为正确的值
        let keys = Object.keys(curItem);
        if (keys.length === 0) {
          alert("数据为{}");
          this.tableData = [];
          this.receptionUrl = "";
          return;
        }
        for (let key in curItem) {
          if (!/^\d{4}-\d{2}-\d{2}$/g.test(key)) {
            // alert(curItem[key]);
            // 设置显示文本
            this.showMsg = curItem[key];
            this.tableData = [];
            this.receptionUrl = "";
            console.log("跳出循环，跳出方法");
            return;
          }
          let obj = {};
          obj["dates"] = key;
          console.log(curItem[key][0]);
          for (let i in curItem[key][0]) {
            let value = curItem[key][0][i];
            // ranking small_rank star_level comment_amount
            if (
              i === "ranking" ||
              i === "small_rank" ||
              i === "star_level" ||
              i === "comment_amount"
            ) {
              if (typeof value === "string") {
                console.log("同步值", typeof value, value);
                value = value.replace(/^(\D*)(\d+.*)/g, "$2");
                console.log("同步值", value);
              }
            }
            obj[i] = value;
          }
          if (this.tableData.length === 0) {
            this.receptionUrl = obj.URL;
          }
          this.tableData.push(obj);
        }
      }

      // let curItem = {};
      // for (let key in this.firstData) {
      //   if (key === item) {
      //     curItem = this.firstData[key];
      //     break;
      //   }
      // }
      // console.log("选中的表格数据", curItem);
      // // 判断选中的键是否为正确的值
      // let keys = Object.keys(curItem);
      // if (keys.length === 0) {
      //   alert("数据为{}");
      //   this.tableData = [];
      //   return;
      // }
      // for (let key in curItem) {
      //   if (!/^\d{4}-\d{2}-\d{2}$/g.test(key)) {
      //     alert(curItem[key]);
      //     this.tableData = [];
      //     return;
      //   }
      //   let obj = {};
      //   obj["dates"] = key;
      //   console.log(curItem[key][0]);
      //   for (let i in curItem[key][0]) {
      //     let value = curItem[key][0][i];

      //     obj[i] = value;
      //   }

      //   this.tableData.push(obj);
      //   this.receptionUrl = this.tableData[0].URL;
      // }
    },
    handleselect(key) {
      let menuArr = key.split("-");
      this.thirdIdx = menuArr.length ? menuArr[2] : "";
      console.log(this.thirdIdx);
      this.drawerVisible = false;
      this.isShowSku = true;
      if (menuArr.length === 3) {
        this.openSku(this.thirdIdx);
      }
    },
    handleOpen(key) {
      console.log(key);
      let menuArr = key.split("-");
      console.log(menuArr);
      this.firstIdx = menuArr.length ? menuArr[0] : "";
      this.secondIdx = menuArr.length ? menuArr[1] : "";
      if (menuArr.length === 1) {
        this.openStation(this.firstIdx);
      }
      if (menuArr.length === 2) {
        // 二级目录
        this.openCountry(this.secondIdx);
      }
    },
    cellStyle({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 1 || columnIndex === 4) {
        return {
          color: "blue"
        };
      }
    },
    editSubmit(formname) {
      if (!this.spu) return;
      this.$refs[formname].validate(valid => {
        if (valid) {
          if (!this.submitYet) {
            // 如果没有提交过，要求必须输入自动组和手动组
            if (
              this.formData.automatic_guidance === "" ||
              this.formData.manual_guidance === ""
            ) {
              alert("请输入自动组和手动组！");
              return;
            }
          } else {
            if (
              this.formData.automatic_guidance === "" &&
              this.formData.manual_guidance === "" &&
              this.formData.remarks === ""
            ) {
              return;
            }
          }
          let len = this.tableData1.length - 1;
          this.id = this.tableData1[len].id;
          let url = this._url2 + "server/order_management/advertising_guide/";
          this.$axios
            .put(url, {
              site: this.table2_lastObj["company"],
              country: this.table2_lastObj["countries"],
              // id: this.id,
              // company: this.table2_lastObj["company"],
              // countries: this.table2_lastObj["countries"],
              dates: this.date || this.table2_lastObj["times"],
              spu: this.spu,
              auto: this.auto,
              manual_guidance: this.formData.manual_guidance,
              automatic_guidance: this.formData.automatic_guidance,
              remakes: this.formData.remarks
            })
            .then(res => {
              if (res.data.code === 200) {
                // 已经提交过
                this.submitYet = true;
                // 刷新页面
                this.formData.automatic_guidance = "";
                this.formData.manual_guidance = "";
                this.formData.remarks = "";
                console.log("spu值", this.spu);
                this.selectSku(this.curIndex);
              } else {
                alert("提交出现问题");
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    getMenu() {
      this.getStation();
    },
    getStation() {
      this.menuPlatFormList = [];
      let url = this._url2 + "server/personnel_management/get_sign_area1/";
      this.$axios.get(url).then(res => {
        console.log(res);
        // this.allChannelData = res.data.data;
        this.menuPlatFormList.push(res.data.data1);
        this.showCountryList = res.data.data || [];
      });
      // this.$axios
      //   .get(this._url2 + "server/personnel_management/get_sign_country1/")
      //   .then(res => {
      //     console.log(res);
      //   });
    },
    openStation(channelIndex) {
      this.channel = this.menuPlatFormList[channelIndex];
    },
    openCountry(countryIndex) {
      if (
        this.openCountryFlag &&
        this.station === this.showCountryList[countryIndex]
      ) {
        console.log("关闭");
        this.openCountryFlag = false;
        return;
      }
      this.openCountryFlag = true;
      this.station = this.showCountryList[countryIndex];

      this.openCountryFlag = !this.openCountryFlag;
      this.curIndex = "";
      this.lastSiteList = [];

      this.countryListIndex = countryIndex;
      // 发送请求
      this.getCountry(this.station);
    },
    // 刷新到sku
    // skuReflash(){

    // },
    selectSku(index, item) {
      // this.spu = "";
      this.loading = true;
      // this.loading2 = true;
      this.submitYet = false;
      this.curIndex = index;
      this.curIndex1 = "";
      if (item) {
        console.log("auto", item);
        this.auto = item;
        let selectItem = this.spuData.find(i => item === i.auto);
        console.log("选中的spu", selectItem.spu);
        this.spulist = selectItem.spu.split(","); // spu列表
        console.log("spu列表", this.spulist);
        this.spu = this.spulist[0];
      }

      console.log("spu打印", this.spu);
      let url = this._url2 + "server/order_management/advertising_guide/";
      this.$axios
        .get(url, {
          params: {
            country: this.country,
            site: this.station,
            auto: this.auto,
            spu: this.spu
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            this.loading = false;
            this.secondData = res.data.re_data || {};
            this.firstData = res.data.re_spu_data || {};
            this.tableData1 = [];
            this.tableData = [];
            console.log(this.secondData);
            // 获取最后一个键
            let table_key = Object.keys(this.secondData);
            // 表2
            if (table_key.length !== 0) {
              let lastData = table_key.pop(); // 最后一个日期
              this.date = lastData;
              console.log("最后一个对象", this.secondData[lastData]);
              let obj = this.secondData[lastData][0]; // 最后一个对象
              console.log("获取的最后一个对象", lastData);
              for (let key in this.secondData) {
                // 跳过最后一个对象的赋值
                if (key === lastData) {
                  continue;
                }
                console.log(key, lastData);
                let obj = {};
                let item = this.secondData[key][0];
                for (let i in item) {
                  if (i !== "cost_rate" && i !== "times" && i !== "id") {
                    obj[i] = Number(item[i]).toFixed(2);
                  } else {
                    obj[i] = item[i];
                  }
                }
                if (item["sales"] === "" || item["sales"] === undefined) {
                  obj["sys_automatic_guidance"] = "";
                  obj["sys_manual_guidance"] = "";
                } else {
                  obj["sys_automatic_guidance"] = (
                    Number(item["sales"]) * 0.01
                  ).toFixed(2);
                  obj["sys_manual_guidance"] = (
                    Number(item["sales"]) * 0.1
                  ).toFixed(2);
                }

                this.tableData1.push(obj);
              }
              // 遍历最后一个对象
              let lastTableObj = {};
              for (let keyItem in obj) {
                // 实际指导价，实际花费，
                lastTableObj[keyItem] = obj[keyItem];
              }
              this.yesterday_remark = obj.remakes; // 备注
              // 增加系统指导价

              lastTableObj["sys_automatic_guidance"] = (
                Number(lastTableObj["sales"]) * 0.01
              ).toFixed(2);
              lastTableObj["sys_manual_guidance"] = (
                Number(lastTableObj["sales"]) * 0.1
              ).toFixed(2);
              this.tableData1.push(lastTableObj);
              this.table2_lastObj = lastTableObj;
              // 如果最后一个对象的实际指导（自动，手动的值存在则已提交） manual_guidance  automatic_guidance
              if (
                (lastTableObj["automatic_guidance"] !== null &&
                  lastTableObj["automatic_guidance"] !== "") ||
                (lastTableObj["manual_guidance"] !== null &&
                  lastTableObj["manual_guidance"] !== "")
              ) {
                this.submitYet = true;
              }
            }

            // 遍历第一个表格数据
            // this.spulist = [];
            // for (let key in this.firstData) {
            //   console.log("第一个表格数据", key);
            //   this.spulist.push(key); // spu列表
            this.FirstInitSpu = true;
            this.selectSpu(0, this.spulist[0]); // 渲染第一个spu数据
            // }
          } else {
            alert("请求spu数据出错");
          }
        });
    },
    openSku(stationIndex) {
      this.curIndex = "";
      this.curIndex1 = "";
      this.tableData = [];
      this.tableData1 = [];
      this.country = this.lastSiteList[this.secondIdx][stationIndex];
      // 请求sku
      let url = this._url2 + "server/order_management/get_auto_data/";
      this.$axios
        .get(url, {
          params: {
            countries: this.country,
            company: this.station
            // country: this.country,
            // site: this.station
          }
        })
        .then(res => {
          this.sku_list = [];
          // this.sku_list = res.data.spu_num;
          // if (res.data.code === 200) {
          let result = res.data.spu_num;
          this.spuData = result || [];
          if (result.length > 0) {
            for (let key in result) {
              this.sku_list.push(result[key]["auto"]);
              if (key === "0") {
                this.curIndex = 0;
                this.checkedSku = this.sku_list[0];
                this.selectSku(this.curIndex, this.sku_list[this.curIndex]);
              }
            }
          }
          // }
        });
    },
    getCountry(station) {
      let url = this._url2 + "server/personnel_management/get_sign_country1/";
      this.$axios
        .get(url, {
          params: {
            area: station
          }
        })
        .then(res => {
          console.log(res);
          for (let key in this.showCountryList) {
            this.$set(this.lastSiteList, key, []);
          }
          if (res.data.code === 200) {
            this.$set(this.lastSiteList, this.countryListIndex, res.data.data);
          } else {
            alert("请求国家失败");
          }
        });
    }
  },
  created() {
    this.isPhone();
    this.getMenu();
  },
  mounted() {
    this.setWidth();
  }
};
</script>
<style scoped>
.skuSpan {
  font-size: 14px;
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid #666262;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 5px;
}
.skuSpan.active {
  background: #409eff;
  color: #fff;
  border: none;
}
.el-icon-right {
  font-size: 34px;
}
.menu {
  margin-top: 6px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.spu-list {
  margin-top: 12px;
  font-size: 16px;
}
.sku-list {
  font-size: 16px;
}
.sku-list span {
  color: #f56c6c;
}
.table {
  margin-top: 24px;
}
.table-one .table-button {
  text-align: center;
  margin-top: 12px;
}
.table-two {
  margin-top: 24px;
}
.remark .title {
  font-size: 24px;
  text-align: center;
  margin: 12px 0 12px;
}
.remark .editBox {
  /* width: 60%; */
  margin: 0 auto;
  text-align: center;
  margin-bottom: 24px;
}
</style>
<style>
#guide-edit .drawer .el-drawer__header span {
  font-size: 2rem;
  font-weight: bold;
}
/* 去掉抽屉的边框 */
#guide-edit :focus {
  outline: 0;
}
#guide-edit .el-menu {
  max-height: 85vh;
  overflow: auto;
}
#guide-edit .el-table--border td .cell {
  padding-left: 0;
}
@media screen and (max-width: 768px) {
  #guide-edit .remark .el-form--inline .el-form-item__label {
    display: block;
    text-align: left;
  }
  #guide-edit .remark .remark-inline .el-form-item {
    width: 38%;
  }
  #guide-edit .remark .remark-inline .el-form-item__content,
  #guide-edit .remark .remark-width .el-form-item__content {
    width: 100%;
  }
  #guide-edit .remark .remark-width .el-form-item {
    margin: 0;
  }
}
</style>



